<script setup>
import { ref, reactive, onMounted, nextTick } from 'vue';
import { Swiper, SwiperSlide } from 'swiper/vue';
import { Thumbs } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/thumbs';
import { useRoute } from 'vue-router';
import { query, getFirestore, collection, addDoc, doc, getDoc, setDoc, update } from "firebase/firestore";
const route = useRoute();
console.log(route.params.id)
const idKey = route.params.id
const db = getFirestore();
const productData = reactive({
  data: {
    name: "",//商品名稱
    isSize: false,//是否有尺寸可選擇
    isqty: false,//是否現貨
    img: [{ sortIndex: "0", fullpath: "#" }],
    pdSize: [
      {
        productName: "",//規格名稱
        price: "",//商品金額
        sortIndex: 0,//商品排序
        qty: 0,//數量
      }
    ],
    tag: [""],//標籤
    isPreOrder: true,
  }
});
const fetchcData = async () => {
  const q = doc(db, "products", idKey);
  const productSnapshot = await getDoc(q);
  // console.log(productSnapshot)
  if (productSnapshot.exists()) {
    productData.data = productSnapshot.data(); // 將產品資料存入 productData 中
    console.log(productData.data);
    //  判斷金額範圍
    let priceRange="0"
    if(productData.data.pdSize.length==1){
        priceRange=productData.data.pdSize[0].price;
    }else{
     // 找到最大值
    let maxAmount = Math.max(...productData.data.pdSize.map(item => item.price));

    // 找到最小值
    let minAmount = Math.min(...productData.data.pdSize.map(item => item.price));

    console.log("最大值:", maxAmount);
    console.log("最小值:", minAmount);
    if(minAmount===maxAmount){
      pagePrice.value=minAmount;
    }else{
      priceRange=minAmount+"~"+maxAmount;
      pagePrice.value=priceRange;
    }
    
    }
    
    
  } else {
    console.log("找不到指定的產品文檔");
  }
}
const isSelect=ref("")
const updateInfo=(p,sizeName)=>{
  pagePrice.value=p;
  isSelect.value=sizeName;
}
const pagePrice=ref('9999');
////swiper
const onSwiper = (swiper) => {
  swiper = swiper;
};
const thumbsSwiper = ref(null);
const setThumbsSwiper = (swiper) => {
  thumbsSwiper.value = swiper;
}
onMounted(() => {
  fetchcData();



});
const components = {
  Swiper,
  SwiperSlide,
};

</script>
<template>
  <section class="container flex_1">
    <!-- <div class="my-4">
      <input class="keywords" type="text" placeholder="請輸入關鍵字搜尋(品名)">
    </div> -->


    <div class="my-4 row">
      <div class="col-12">

      </div>
      <div class="col-12 col-md-6">
        <div class="row">
          <div class="col-12 col-lg-9">
            <swiper @swiper="onSwiper" @slideChange="setThumbsSwiper"  :modules="[Thumbs]" :thumbs="{ swiper: thumbsSwiper } " class="swiper">
              <swiper-slide v-for="(item, idx) in productData.data.img" :key="idx" >
                <img :src="item.fullpath" />
              </swiper-slide>
              <swiper-slide>Slide 2</swiper-slide>
              <swiper-slide>Slide 3</swiper-slide>
              <swiper-slide>Slide 1</swiper-slide>
              <swiper-slide>Slide 2</swiper-slide>


            </swiper>
          </div>
          <div class="col-12 col-lg-3 d-none d-lg-block">
            <swiper  :slides-per-view="3" :spaceBetween="18" :modules="[Thumbs] " class="swiper swiper-autoHeight" direction="vertical"
    watch-slides-progress
    @swiper="setThumbsSwiper">
              <swiper-slide v-for="(item, idx) in productData.data.img" :key="idx">
                <img :src="item.fullpath" />
              </swiper-slide>
              <swiper-slide>Slide 2</swiper-slide>
              <swiper-slide>Slide 3</swiper-slide>
              <swiper-slide>Slide 1</swiper-slide>
              <swiper-slide>Slide 2</swiper-slide>


            </swiper>
          </div>
        </div>
      </div>


      <div class="col-12 col-md-6" v-if="productData.data">

        <h2>
          <span class="titleTags" v-if="productData.data.isPreOrder">預購</span>
          <span class="titleTags isQty" v-if="productData.data.isqty">現貨</span>
          <span class="titleTags nonQty" v-if="!productData.data.isqty">無庫存
          </span>
          {{ productData.data.name }}
        </h2>
        <div>標籤索引:
          <div><span v-for="(item, idx) in productData.data.tag" :key="idx">#{{ item }}</span></div>
        </div>
        <div>商品規格:</div>
        <div><input v-for="(item, idx) in productData.data.pdSize" :class="['btn', 'btn-norm', 'btn-size',{'act':isSelect===item.productName}]" type="button"
            :value="item.productName" @click="updateInfo(item.price,item.productName)" :key="idx">{{ item }}</div>
        <div>數量:</div>
        <div class="group-quantity">
          <button class="btn-quantity">–</button>
          <input class="input-quantity" type="text" value="1">
          <button class="btn-quantity">+</button>
        </div>
        <div>注意事項:預購商品如有尺碼選擇，一律要求匯款定金。</div>
        <div class="text-sm d-none">
          注意事項: 預購商品請先付清全款，如無法先付清全款可先選擇登記預購，到貨時通知下單，金額依現貨價計算。
        </div>
        <div class="row">
          <div class="col-6">
            <p class="text-sm">預購價</p>
            <p class="text-price">NT${{pagePrice}}</p>
            <input type="button" class="btn-main disabled" value="登記預購請至粉絲團私訊">

          </div>
          <div class="col-6 d-none">
            <p class="text-sm">預購價</p>
            <p class="text-price">NT$950</p>
            <input type="button" class="btn-main disabled" value="目前無現貨" disabled>
          </div>


        </div>
      </div>

    </div>

  </section>
</template>
<style lang="scss" scoped>
.swiper-slide {
  border-radius: 6px;
  border: solid 1px #ddd;
  aspect-ratio: 1 / 1;
}
.swiper-slide img {
  aspect-ratio: 1 / 1;
}

.btn-size {
  color: #1f6367;
  border-color: #1f6367;
}
.btn-size:hover {
  color: #fff;
  background-color: rgb(92 203 210 / var(--tw-bg-opacity));
  border-color: rgb(92 203 210 / var(--tw-bg-opacity));
}
.btn-size.act {
  color: #fff;
  background-color: #1f6367;
  border-color: #1f6367;
}

h2 {
  color: #111;
}

.titleTags {
  background-color: #4acfc9;
  color: #fff;
  padding: 0px 6px;
  border-radius: 4px;
  margin-right: 4px;
}

.isQty {
  background-color: #e2404d
}

.nonQty {
  background-color: #afafaf;
}

.swiper-autoHeight {
  @apply lg:h-[348px] xl:h-[430px] 2xl:h-[500px];
  // @media screen and (min-width:992px){
  //     height:348px
  // }
  // @media screen and (min-width:1280px){
  //     height:452px
  // }
  // @media screen and (min-width:1580px){
  //     height:569px
  // }

}
.btn-main.disabled{
  background-color: #999!important;
}
</style>